import TextField from '@material-ui/core/TextField';
import { useCallback, useRef, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import CheckButton from 'react-validation/build/button';
import Form from 'react-validation/build/form';
import { forgetPasswordRequest } from '../../services/auth.service';
import { Button } from '../common';
import './ResetPassword.scss';
import { FLOWHERO } from '../../shared/utility';
import { isMobile } from 'react-device-detect';

const ResetPassword = () => {
	const navigate = useNavigate();
	const form = useRef();
	const checkBtn = useRef();
	const [email, setEmail] = useState('');
	const [loading, setLoading] = useState(false);
	const [, setShowHelp] = useState(false);
	const [message, setMessage] = useState('');
	const [sent, setSent] = useState(false);

	const handleRequest = useCallback(
		e => {
			e.preventDefault();
			setLoading(true);
			form.current.validateAll();
			if (checkBtn.current.context._errors.length === 0) {
				forgetPasswordRequest(email).subscribe({
					next: _data => {
						setSent(true);
						setMessage('');
					},
					error: error => {
						console.log(error);
						setSent(false);
						setMessage(error.data);
						setLoading(false);
					},
					complete: () => {
						setLoading(false);
					},
				});
			} else {
				setLoading(false);
			}
		},
		[email]
	);

	return (
		<div>
			<p
				onClick={() => navigate('/login')}
				style={{
					alignSelf: 'flex-start',
					cursor: 'pointer',
					padding: '10px',
					fontFamily: 'var(--app-text-main-font)',
					color: '#4A4A4A',
					textDecoration: 'underline',
				}}
			>
				{`Login`}
			</p>

			<div id="reset-password-container" className="main-container">
				<div style={{margin: '5px', padding: '30px', border: '0.5px solid gray' }}>
					<div
						style={{
							display: 'flex',
							width: '100%',
							flexDirection: 'column',
						}}
					>
						<div
							style={{
								alignSelf: 'flex-start',
								display: 'flex',
								flexDirection: 'column',
								justifyContent: 'space-evenly',
								alignItems: 'center',
							}}
						>
							<div style={{ alignSelf: 'flex-start', width: isMobile ? '100%':'350px' }}>
								{FLOWHERO(isMobile? '26px':'26px', 'var(--app-color-gray-dark)', 'bold')}
								<h3
									style={{
										color: '#4A4A4A',
										fontFamily: 'var(--app-text-main-font)',
										fontSize: isMobile ? '22px' : '24px',
										fontWeight: 500,
										letterSpacing: '0',
										marginBottom: '0',
									}}
								>
									{sent && 'Password Reset Sent!'}
								</h3>
								<p
									style={{
										color: '#4A4A4A',
										fontFamily: 'var(--app-text-main-font)',
										fontSize: '16px',
										fontWeight: 500,
									}}
								>
									{sent ? 'Follow the instructions in the email.' : 'To reset your password, input your company email in the field below and follow the instructions in the email sent.'}
								</p>
							</div>
							<div style={{ padding: '30px 5px 30px', width: '100%' }}>
								<Form onSubmit={handleRequest} ref={form}>
									{!sent && <TextField id="my-input" type="text" variant="outlined" name="name" className="input-style" placeholder={'Company Email'} value={email} onChange={e => setEmail(e.target.value)} onFocus={() => setMessage('')} />}
									{message?.email ? (
										<div className="form-group">
											<div className={'message-container'} role="alert">
												{message?.email}
											</div>
										</div>
									) : null}
									<div
										style={{
											marginTop: sent ? '0px' : '20px',
										}}
									>
										<Button disabled={loading} loading={loading} clicked={handleRequest} title={'Recover'} style={{ margin: '0', width: '100%' }} />
									</div>
									{message && !message.email && (
										<div className="message-container" role="alert">
											{message}
										</div>
									)}
									
								</Form>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	);
};

export default ResetPassword;
